import { urlObjectKeys } from "next/dist/next-server/lib/utils";

class Index extends React.Component {
    render() {
        return (
            <div class="hero-image" style={heroImage}>
                <img src="https://assets.wordpress.envato-static.com/uploads/2016/03/little-girl-super-hero-concept-P7WEDPK.jpg"></img>
            </div>
        )
    }
}
export default Index
const heroImage = {
    // backgroundImage: "url(https://assets.wordpress.envato-static.com/uploads/2016/03/little-girl-super-hero-concept-P7WEDPK.jpg)",

    /* Set a specific height */

    /* Position and center the image to scale nicely on all screens */
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    position: "relative"
}
const heroText = {
    textAlign: "center",
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    color: "white"
}